@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
* {
	font-family: "Muli", sans-serif;
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	background: #3c1d2c;
	/*color: #F99F1F;*/
}

.navbar {
	background-color: ##4D2638 ;/*#4D2638*/
}

.navbar-brand {
	font-size: 1.9rem;
	color: #fff;
}

.navbar a {
	font-size: 1.3rem;
	text-transform: capitalize;
}

/*.navbar a: hover {
	color: #3498db;
}*/

.menu_active {
	font-weight: bold;
	border-bottom: 1px solid #565387;
}

#header {
	width: 100%;
	height: 80vh;
	margin-top: 70px;
}

#header h1 {
	margin: 0 0 10px 0;
	font-size: 48px;
	font-weight: 700;
	color: white;
	position: relative;
	transition: all 1s ease;
}

#header h2 {
	color: #484848;
	font-size: 30px;
	color: rgba(255, 255, 255, 0.5);
	transition: all 1s ease;
}

#header .btn {
	font-weight: 500;
	font-size:16px;
	letter-spacing: 1px;
	display: inline-block;
	padding: 10px 30px;
	border-radius: 50px;
	transition: 0.5s;
	border: 2px solid #3498db;
	text-decoration: none;
}

.header-img {
	text-align: right;
}

#header .animated {
	animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@keyframes up-down {
	0% {
		transform: translateY(20px);
	}
	100px {
		transform: translateY(-20px);
	}
}

@media (max-width: 991px) {
	.navbar-brand {
		font-size: 1.5rem;
	}

	.menu_active {
		font-weight: bold;
		border-bottom: none;
	}
}



.App {
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  width: 100%;
  background: #4D2638;
  display: flex;
  align-items: center;
  z-index: -1;
  transition: all 0.5s ease; 
}

.App-logo {
   width: 100px;
  margin: auto;
}

